let app = getApp();
var active = {
	'active': app.globalData.active
};
var rootDocment = app.globalData.rootUrl + '/mobileOrder/'; //; //
var globalUrl = ["login"]
if (uni.getStorageSync("cdj_token")) {
	var header = {
		'Accept': 'application/json',
		'content-type': 'application/json', //
		'Authorization': uni.getStorageSync("cdj_token"),
	}
}
/***
 * uri: 
 * datas:
 * success:
 * fail:
 */
//get
function getRequest(url, datas, success) {
	uni.showLoading({
		title: '...',
		duration: 2000,
		mask: true,
		success: function(res) {
			uni.request({
				url: rootDocment + url,
				method: 'GET',
				header: {
					'Accept': 'application/json',
					'content-type': 'application/json', //
					'Authorization': uni.getStorageSync("cdj_token"),
				},
				data: Object.assign(datas, active),
				success: res => {
					success(res)
					if (res.header.Authorization != undefined) {
						uni.setStorageSync("cdj_token", res.header.Authorization)
					}
					if (res.data.code == 400) {
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 1000,
							success: function() {

							}
						})
					}
					if (res.data.code == 401) {
						uni.navigateTo({
							url: '/pages/account/login'
						});
					}
					if (res.data.code == 404) {

						uni.navigateTo({
							url: '/pages/account/404'
						});

					}

					uni.hideLoading();
				},
				fail: res => {
					uni.showModal({
						title: res.data,
						content: '',
						showCancel: false
					})
				},

			})
		},
		fail: function(res) {},
		complete: function(res) {},
	})


}

//get
function getRequests(url, datas, success) {
	uni.request({
		url: rootDocment + url,
		method: 'GET',
		header: {
			'Accept': 'application/json',
			'content-type': 'application/json',
			'Authorization': uni.getStorageSync("cdj_token"),
		},
		data: Object.assign(datas, active),
		success: res => {
			success(res)
			if (res.header.Authorization != undefined) {
				uni.setStorageSync("cdj_token", res.header.Authorization)
			}
			if (res.data.code == 401) {
				uni.navigateTo({
					url: '/pages/account/login'
				});
			}
			if (res.data.code == 404) {
				uni.navigateTo({
					url: '/pages/account/404'
				});

			}
			if (res.data.code == 408) {
				uni.navigateTo({
					url: '/pages/account/service'
				})
			}
		},
		fail: res => {
			uni.showModal({
				title: res.data,
				content: '',
				showCancel: false
			})
		},

	})
}

