function objKeySort(obj) { //
	var newkey = Object.keys(obj).sort();
	//ObjectkeysArraysortnewkey
	var newObj = {}; //
	var sz = '';
	for (var i = 0; i < newkey.length; i++) { //newkey
		newObj[newkey[i]] = obj[newkey[i]]; //
	}
	Object.keys(newObj).forEach(function(key) {
		sz += '&' + key + '=' + newObj[key]
	});
	return sz.substr(1); //
}


function Toast(message) {
	uni.showToast({
		title: message,
		icon: 'none',
		duration: 1000
	})
}


function getLastDay() {
	var current = new Date();
	var currentMonth = current.getMonth();
	var nextMonth = ++currentMonth;

	var nextMonthDayOne = new Date(current.getFullYear(), nextMonth, 1);

	var minusDate = 1000 * 60 * 60 * 24;

	return new Date(nextMonthDayOne.getTime() - minusDate);
}

function thedefaulttime() { //
	var date = new Date();
	var year = date.getFullYear().toString();
	var time = (date.getMonth() + 1).toString();


	var month = '';
	if (time < 10) {
		month = "0" + time;
	} else {
		month = time;
	}
	var num = date.getDate().toString();
	var day = getLastDay().getDate();

	// if (num < 10) {
	// 	day = "0" + num;
	// } else {
	// 	day = num;
	// }
	var start = year + '-' + month + '-01';
	var end = year + '-' + month + '-' + day;
	var dateArr = [start, end];
	return dateArr;
}

